.button {
  outline: none;
  border: none;
  padding: 25px 80px;
  background-color: #ffae19;
  border-radius: 10px;
  margin: 0 auto;
  display: block;
  position: relative;
  font-size: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  box-shadow: 0 6px #ffa500;
  color: white;
  width: 50px;
  height: 50px;
}

.button:hover {
  box-shadow: 0 4px #ffa500;
  top: 2px;
}

.button:active {
  box-shadow: none;
  top: 6px;
}

