@import url('https://fonts.googleapis.com/css2?family=Asap+Condensed:ital,wght@0,400;0,500;0,600;1,400&family=Asap:ital,wght@0,400;0,500;1,400&display=swap');

.Footer {
  display:flex;
  flex-direction:row;
  justify-content: center;
  padding:30px;
}

h4 {
    color:#4F1011;
    font-family: 'Asap', sans-serif;
    font-size: 1.2rem;
    font-weight: 500;
    align-items: center;
}


 /* I Pad Pro */
@media only screen and (min-height: 1025px) and (max-height: 3160px) {
  .Footer {
    padding:20px;
  }
}

/* Desktop */
@media only screen and (min-width: 1221px)  and (orientation: landscape) {
  .Footer {
    padding:5px;
  }
}
